import React, { useState, useEffect, useMemo, useCallback } from 'react'

import CopyOutlined from '@ant-design/icons/lib/icons/CopyOutlined'
import {
  ConfirmModal,
  ContentTitle,
  CoreButton,
  Helmet,
  Input,
  Tooltip,
  Notification,
  AffiliateInput,
} from 'components'
import copy from 'copy-to-clipboard'
import { encode } from 'jwt-simple'
import { getWalletAddressByChain } from 'multichain-sdk'

import { ADDRESS } from 'multichain-sdk/config'

import { useBalance } from 'hooks/useBalance'

import { multichain } from 'services/multichain'

import { getAffiliates } from 'helpers/storage'

import * as Styled from './Affiliate.style'

const Affiliate = () => {
  const [recipient, setRecipient] = useState('')
  const [chainRecipient, setChainRecipient] = useState('')
  const [link, setLink] = useState('')
  const { wallet } = useBalance()
  const [visibleConfirmModal, setVisibleConfirmModal] = useState(false)
  const handleCopyAddress = useCallback(() => {
    copy(link)

    Notification({
      type: 'info',
      message: 'Address Copied',
      duration: 3,
      placement: 'bottomRight',
    })
  }, [link])
  const getAffiliateLink = useMemo(() => {
    return (
      <Styled.ConfirmModalContent>
        <ContentTitle>Become An Affiliate</ContentTitle>
        <Input disabled value={link} />
        <Tooltip placement="top" tooltip="Copy">
          <CoreButton onClick={handleCopyAddress}>
            <CopyOutlined />
          </CoreButton>
        </Tooltip>
      </Styled.ConfirmModalContent>
    )
  }, [handleCopyAddress, link])
  useEffect(() => {
    if (wallet) {
      const address = getWalletAddressByChain(wallet, 'THOR')
      setRecipient(address || '')
      setChainRecipient(address || '')
    }
  }, [wallet])
  const handleSend = () => {
    // get affiliates from local storage
    const refs = getAffiliates()
    // decrypt affiliate array
    const affiliateAddresses: string[] = refs ?? []
    affiliateAddresses.push(recipient)
    const filteredAddresses = affiliateAddresses.filter((affiliateAddress) =>
      multichain.validateAddress({
        chain: 'THOR',
        address: affiliateAddress,
      }),
    )
    if (
      multichain.validateAddress({
        chain: 'THOR',
        address: recipient,
      })
    ) {
      // encrypt affilate array
      const encodedQuery = encode(
        JSON.stringify(filteredAddresses),
        ADDRESS as string,
      )
      setLink(`https://crosschain.quest/#/?refs=${encodedQuery}`)
      setVisibleConfirmModal(true)
    } else {
      Notification({
        type: 'error',
        message: 'Invalide Thorchain address',
        description: 'Affiliate links must be valid thorchain addresses',
        duration: 20,
      })
    }
  }
  const handleClose = useCallback(() => {
    setVisibleConfirmModal(false)
  }, [])

  return (
    <Styled.Container>
      <Helmet title="Sign up for " content="Sign up for" />
      <Styled.FromCard>
        <ContentTitle>Become An Affiliate</ContentTitle>
        <AffiliateInput
          title="address"
          address={recipient}
          chain="THOR"
          chainAddr={chainRecipient}
          onAddressChange={setRecipient}
        />
        <Styled.GenerateButton onClick={handleSend} error={false}>
          Generate Link
        </Styled.GenerateButton>
      </Styled.FromCard>
      <ConfirmModal
        visible={visibleConfirmModal}
        onOk={handleClose}
        inputAssets={[]}
        onCancel={handleClose}
      >
        {visibleConfirmModal && getAffiliateLink}
      </ConfirmModal>
    </Styled.Container>
  )
}

export default Affiliate
